<template>
  <!-- <v-container> -->
  <v-row class="mx-auto flex-nowrap">
    <v-col md="3">
      <v-card outlined v-show="blocks.length">
        <v-sheet class="pa-4 grey darken-4">
          <!-- append-icon="mdi-dots-vertical" -->
          <v-text-field
            v-model="treeView.search"
            prepend-inner-icon="mdi-magnify"
            placeholder="Search ..."
            background-color="grey darken-3"
            flat
            dense
            solo
            hide-details
            clearable
          >
            <template v-slot:append>
              <v-menu
                v-model="treeViewSearchMenu.show"
                :close-on-content-click="false"
                :nudge-width="200"
                offset-x
              >
                <template v-slot:activator="{ on }">
                  <v-btn icon v-on="on">
                    <v-icon>mdi-dots-vertical</v-icon>
                  </v-btn>
                </template>

                <v-card>
                  <v-list>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>Search Settings</v-list-item-title>
                        <v-list-item-subtitle
                          >Choose which field to search</v-list-item-subtitle
                        >
                      </v-list-item-content>
                    </v-list-item>
                  </v-list>
                  <v-divider></v-divider>
                  <v-list>
                    <v-list-item
                      v-for="key in treeViewSearchMenu.searchKeys"
                      :key="key.text"
                      dense
                    >
                      <v-list-item-action>
                        <v-switch
                          dense
                          v-model="key.enable"
                          :color="treeViewSearchMenu.switchColor"
                        ></v-switch>
                      </v-list-item-action>
                      <v-list-item-title class="text-capitalize">{{
                        key.text
                      }}</v-list-item-title>
                    </v-list-item>
                  </v-list>

                  <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn
                      color="primary"
                      text
                      @click="treeViewSearchMenu.show = false"
                      >Close</v-btn
                    >
                  </v-card-actions>
                </v-card>
              </v-menu>
            </template>
          </v-text-field>
        </v-sheet>
        <v-card-actions>
          <v-btn @click="treeViewDeselectAll" small depressed
            >Deselect All</v-btn
          >
        </v-card-actions>
        <!-- item-children="registers" -->
        <v-card-text>
          <v-treeview
            selected-color="light-blue darken-2"
            v-model="treeView.selected"
            :items="blockFiltered"
            :filter="treeViewFilter"
            :search="treeView.search"
            item-children="registers"
            dense
            selectable
            open-on-click
            hoverable
            return-object
            ref="treeReference"
          >
            <template v-slot:prepend="{ item, leaf }">
              <v-chip
                small
                v-if="leaf"
                pill
                class="text-uppercase subtitle-2 "
                color="grey lighten-1"
                text-color="black"
              >
                {{ item.address }}
              </v-chip>
            </template>
          </v-treeview>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col md="8">
      <AnalyzerTables
        :parents="blocks"
        :registers="treeView.selected"
        :headers="headers"
        :highlight="treeView.search"
      />
    </v-col>
    <v-col md="1">
      <!-- <v-treeview
        v-model="selectedBlock"
        :items="registerProfile"
        :open.sync="open"
        dense
        selectable
        open-on-click
        hoverable
        return-object
      ></v-treeview> -->
      <!-- <v-speed-dial top>
        <v-icon>mdi-account-circle</v-icon>
      </v-speed-dial> -->
    </v-col>
  </v-row>

  <!-- </v-container> -->
</template>

<script>
// import registers from '@/data/data.json'
import AnalyzerTables from '@/components/AnalyzerTables'

export default {
  name: 'HelloWorld',
  props: {
    blocks: {
      type: Array,
      required: true
    }
  },
  components: {
    AnalyzerTables
  },
  data() {
    return {
      headers: ['MSB', 'LSB', 'field', 'default', 'access', 'description'],
      treeView: {
        selected: [],
        keys: ['address', 'name', 'description'],
        search: null
      },
      treeViewSearchMenu: {
        show: false,
        searchKeys: {},
        switchColor: 'primary'
      }
    }
  },
  mounted() {
    this.treeView.keys.forEach(key => {
      this.setSearchKeys(key, 'Register')
    })
    this.headers.forEach(key => {
      this.setSearchKeys(key, 'Field')
    })
  },
  methods: {
    setSearchKeys(key, prefix) {
      key = this.SearchKeyFormat(key, prefix)
      this.treeViewSearchMenu.searchKeys[key] = {
        enable: true,
        text: key
      }
    },
    SearchKeyFormat(key, prefix) {
      return `${prefix}.${key}`
    },
    treeViewDeselectAll() {
      this.treeView.selected = []
    },
    keyFilter(keys, regxp, item, prefix) {
      const searchKeys = this.treeViewSearchMenu.searchKeys
      for (const key of keys) {
        const is_enable = (searchKeys[this.SearchKeyFormat(key, prefix)] || {})
          .enable
        if (!is_enable) continue
        if (regxp.test(item[key])) return true
      }
      // console.log(item)
      return false
    },
    leafFilter(regxp, leaf) {
      // if (this.keyFilter(this.treeView.keys, regxp, leaf)) return true
      if (leaf.fields) {
        for (const each_field of leaf.fields) {
          if (this.keyFilter(this.headers, regxp, each_field, 'Field'))
            return true
        }
      } else {
        return false
      }
    },
    blockFilter(regxp, nodes) {
      this.treeViewDeselectAll()
      const newBlocks = nodes.reduce((result, node) => {
        if (this.keyFilter(this.treeView.keys, regxp, node, 'Register')) {
          result.push(node)
          return result
        }
        if (!node.registers) {
          // a leaf cell
          if (this.leafFilter(regxp, node)) result.push(node)
        } else {
          // not a leaf cell
          const children = this.blockFilter(regxp, node.registers)

          if (children.length)
            result.push({ ...node, ...{ registers: children } })
        }
        return result
      }, [])

      return newBlocks
    },
    treeViewFilter(item, text) {
      try {
        let regxp = RegExp(text, 'i')
        if (this.keyFilter(this.treeView.keys, regxp, item, 'Register')) {
          return true
        }
        if (!item.registers) {
          // a leaf cell
          if (this.leafFilter(regxp, item)) return true
        }
        return false
      } catch (error) {
        return false
      }
    }
  },
  computed: {
    blockFiltered() {
      // return this.blocks.map(item => {
      //   item.children = item.registers.slice(0, 50)
      //   return item
      // })
      return this.blocks
    }
  }
}
</script>
