function reverseString(str) {
  return str
    .split('')
    .reverse()
    .join('')
}

const ConvertBase = function(value, from, to, width = undefined) {
  var mutation = parseInt(value, from).toString(to)
  if (!width) {
    width = 0
  }
  width = Math.ceil(width)
  mutation = mutation.padStart(width, 0)
  return mutation
}

const hexToBinary = (value, width = undefined) => {
  const binary = ConvertBase(value, 16, 2, width)
  const binaryReversed = reverseString(binary)
  if (!width) {
    return binary
  } else {
    return reverseString(binaryReversed.slice(0, width))
  }
}

const binaryToHex = (value, width = undefined) => {
  return ConvertBase(value, 2, 16, width)
}

export default {
  hexToBinary,
  binaryToHex
}
