<template>
  <div>
    <v-row v-for="register in registerFiltered" :key="register.id" no-gutters>
      <!-- <v-expansion-panels hover tile> -->
      <v-col>
        <AnalyzerTable
          :key="register.id"
          :register="register"
          :headers="headers"
          :title="setTitle(register)"
          :highlight="highlight"
          @close="closeIconAction"
          class="mb-3"
        >
          <template v-slot:title>
            <!-- <v-badge :content="register.address"> -->

            <!-- </v-badge> -->
            <v-chip
              pill
              small
              class="mr-2 text-uppercase subtitle-2"
              color="grey lighten-1"
              text-color="black"
            >
              {{ register.address }}
            </v-chip>
            <span
              v-html="
                $options.filters.highlight(
                  `${getParentByID(register.id)} / ${register.name}`,
                  highlight
                )
              "
            >
            </span>

            <!-- {{
              `${getParentByID(register.id)} /
              ${register.name} (${register.address})`
            }} -->
          </template>
        </AnalyzerTable>
      </v-col>
      <!-- </v-expansion-panels> -->
      <!-- <v-pagination
      v-model="page"
      :length="pageLength"
      total-visible="6"
      class="footer"
    ></v-pagination> -->
    </v-row>
  </div>
</template>

<script>
// import AnalyzerTable from '@/components/AnalyzerTable'
import AnalyzerTable from '@/components/AnalyzerTable2'
export default {
  components: {
    AnalyzerTable
  },
  props: {
    registers: {
      type: Array,
      default: null
    },
    headers: {
      type: Array,
      default: null
    },
    parents: {
      type: Array,
      default: null
    },
    highlight: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      panel: [],
      entry: 3,
      page: 1,
      length: 6
    }
  },
  methods: {
    closeIconAction(id) {
      const index = this.registers.findIndex(item => item.id === id)
      this.registers.splice(index, 1)
      this.panel = this.panel.map(item => (item > index ? item-- : item))
    },
    getParentByID(id) {
      var parent = this.parents.find(function(parent) {
        if (parent.registers.length) {
          return parent.registers.find(register => register.id === id)
        }
        return false
      })
      return (parent || '').name
    },
    setTitle(register) {
      return `${this.getParentByID(register.id)} /
              ${register.name} (${register.address})`
    }
  },
  computed: {
    slicedRegisters() {
      // const start = (this.page - 1) * this.entry
      // return this.registers.slice(start, start + this.entry)
      return this.registers
    },
    registerFiltered() {
      return this.registers.filter(item => {
        return item.hasOwnProperty('fields')
      })
    }
    // pageLength() {
    //   return Math.ceil(this.registers.length / this.entry)
    // }
  }
}
</script>

<style scoped>
