<template>
  <v-app>
    <!-- app navigation-drawer -->
    <v-navigation-drawer
      ref="drawer"
      right
      app
      clipped
      v-model="drawer.is_open"
      :width="drawer.width"
    >
      <v-card flat color="#363636">
        <v-toolbar flat dense>
          <v-btn icon @click.stop="drawer.is_open = false">
            <v-icon>mdi-chevron-right</v-icon>
          </v-btn>
          <v-toolbar-title>
            {{ toolbar.name }}
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon @click.stop="textField.show = !textField.show">
            <v-icon>mdi-magnify</v-icon>
          </v-btn>
        </v-toolbar>
        <v-text-field
          outlined
          flat
          dense
          class="ma-4"
          clearable
          hide-details
          placeholder="Search ..."
          v-model="treeView.search"
          v-show="textField.show"
        ></v-text-field>

        <v-treeview
          v-model="treeView.value"
          :items="treeView.items"
          :search="treeView.search"
          selected-color="light-blue darken-2"
          dense
          selectable
          open-on-click
          hoverable
          return-object
          :open.sync="treeViewOpen"
        ></v-treeview>
      </v-card>
      <v-btn
        fab
        fixed
        x-small
        depressed
        dark
        bottom
        left
        @click="$vuetify.goTo(dial.offsetTop, dial.options)"
      >
        <v-icon>mdi-chevron-up</v-icon>
        <!-- {{ dial.offsetTop }} -->
      </v-btn>
    </v-navigation-drawer>

    <!-- app tool bar -->
    <v-app-bar app clipped-right clipped-left>
      <v-toolbar-title>RegisterProfile</v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <div class="d-flex align-center"> -->
      <v-file-input
        accept=".xls, .xlsx, .json"
        placeholder="Upload your register profile"
        full-width
        small-chips
        dense
        :loading="fileInput.loading"
        :disabled="fileInput.disabled"
        truncate-length="40"
        :error="fileInput.error"
        :error-messages="fileInput.errorMSG"
        @click:clear="clearTreeView"
        @change="fileUploaded"
      ></v-file-input>
      <!-- </div> -->
      <v-spacer></v-spacer>
      <v-btn text @click="drawer.is_open = !drawer.is_open">
        <span class="mr-2" v-text="toolbar.name"></span>
        <v-icon v-show="drawer.is_open">mdi-eye</v-icon>
        <v-icon v-show="!drawer.is_open">mdi-eye-off</v-icon>
      </v-btn>
    </v-app-bar>

    <!-- app content -->
    <v-content>
      <HelloWorld :blocks="treeView.value" />
    </v-content>
  </v-app>
</template>

<script>
import HelloWorld from '@/components/HelloWorld'
import registers from '@/data/data.json'
import ExcelReader from '@/IOHandler/ExcelReader'

export default {
  name: 'App',

  components: {
    HelloWorld
  },
  data() {
    return {
      toolbar: {
        name: 'Hierarchy View'
      },
      drawer: {
        width: 300,
        is_open: true,
        borderSize: 3
      },
      treeView: {
        items: [registers],
        value: [],
        search: null
      },
      textField: {
        show: false
      },
      fileInput: {
        loading: false,
        disabled: false,
        error: false,
        errorMSG: ''
      },
      dial: {
        offsetTop: 0,
        options: {
          duration: 500,
          easing: 'easeInOutCubic'
        }
      }
    }
  },
  mounted() {
    this.$vuetify.theme.dark = true
    this.setBorderWidth()
    this.setEvents()
  },
  methods: {
    setBorderWidth() {
      let i = this.$refs.drawer.$el.querySelector(
        '.v-navigation-drawer__border'
      )
      i.style.width = this.drawer.borderSize + 'px'
      i.style.cursor = 'ew-resize'
    },
    setEvents() {
      const minSize = this.drawer.borderSize
      const el = this.$refs.drawer.$el
      const drawerBorder = el.querySelector('.v-navigation-drawer__border')
      const vm = this
      const direction = el.classList.contains('v-navigation-drawer--right')
        ? 'right'
        : 'left'

      function resize(e) {
        document.body.style.cursor = 'ew-resize'
        let f =
          direction === 'right'
            ? document.body.scrollWidth - e.clientX
            : e.clientX
        el.style.width = f + 'px'
      }

      drawerBorder.addEventListener(
        'mousedown',
        function(e) {
          if (e.offsetX < minSize) {
            // m_pos = e.x
            el.style.transition = 'initial'
            document.addEventListener('mousemove', resize, false)
          }
        },
        false
      )

      document.addEventListener(
        'mouseup',
        function() {
          el.style.transition = ''
          vm.drawer.width = el.style.width
          document.body.style.cursor = ''
          document.removeEventListener('mousemove', resize, false)
        },
        false
      )
    },
    getOpenKey(hier = 0) {
      let toOpen = []
      if (hier === 0) {
        return []
      }
      for (const each_root of this.treeView.items) {
        toOpen.push(each_root)
        for (const children of each_root.children) {
          // toOpen.push(children)
          toOpen = toOpen.concat(this.loopChildren(children, hier))
        }
      }

      return toOpen
    },
    loopChildren(parent, stop = 0) {
      let id = []
      let hier = 1
      id.push(parent)
      if (stop === hier) {
        return id
      }
      for (const children of parent.children) {
        if (children.hasOwnProperty('children')) {
          id = id.concat(this.loopChildren(children, stop - 1))
        }
        // console.log(id)
      }
      // console.log(id)
      return id
    },
    fileUploaded(file) {
      if (!file) return
      this.fileInput.loading = 'cyan'
      this.fileInput.disabled = true
      this.fileInput.error = false
      this.fileInput.errorMSG = ''
      const fileReader = new FileReader()
      const excelReader = new ExcelReader()

      fileReader.onload = e => {
        try {
          // eslint-disable-next-line no-unused-vars
          // var oFile = excelReader.readData(e.target.result, { type: 'binary' })
          excelReader.readData(e.target.result, { type: 'binary' })
          excelReader.getChildren()
          excelReader.root.name = excelReader.root.headpage

          this.treeView.items = excelReader.hasChipIndex
            ? [excelReader.root]
            : excelReader.root.children
        } catch (error) {
          this.fileInput.error = true
          this.fileInput.errorMSG = error
        }

        this.fileInput.loading = false
        this.fileInput.disabled = false
      }
      // function sleep(time) {
      //   return new Promise(resolve => setTimeout(resolve, time))
      // }
      // sleep(3000).then(() => fileReader.readAsBinaryString(file))
      fileReader.readAsBinaryString(file)
    },
    clearTreeView() {
      this.treeView.items = []
    },
    onScroll(e) {
      this.dial.offsetTop = e.target.scrollTop
    }
  },
  computed: {
    treeViewOpen: {
      get() {
        // this.openCache = this.getOpenKey(2)
        // return this.getOpenKey(1)
        return this.getOpenKey(1)
      },
      set() {}
    }
  }
}
</script>
