<template>
  <v-card>
    <v-card-title
      @click.stop="toggleCardContent"
      style="cursor: pointer;"
      @mouseover="hasHover = true"
      @mouseleave="hasHover = false"
      @mousedown.prevent
      :class="{ 'analyzer-card-title-hover': hasHover }"
    >
      <slot name="title"> {{ title }} </slot>
      <v-spacer></v-spacer>

      <v-btn
        class="mx-2"
        small
        depressed
        @click.stop="showDetail = !showDetail"
        v-show="is_open"
      >
        {{ showDetail ? 'Hide Detail' : 'Show Detail' }}
      </v-btn>

      <v-btn
        small
        depressed
        @click.stop="addAnalyzer"
        class="mr-5"
        v-show="is_open"
        >Add Analyzer</v-btn
      >

      <v-btn depressed icon @click.stop="closeAction">
        <v-icon>mdi-close</v-icon></v-btn
      >
    </v-card-title>
    <v-card-subtitle
      style="cursor: pointer;"
      @click.stop="toggleCardContent"
      @mousedown.prevent
      :class="{
        'analyzer-card-title-hover': hasHover,
        'analyzer-card-title-open': is_open,
        'text-truncate': !is_open
      }"
      v-html="$options.filters.highlight(register.description, highlight)"
    ></v-card-subtitle>
    <v-card-text v-show="is_open">
      <v-simple-table class="mx-auto">
        <template v-slot:default>
          <thead>
            <th
              v-for="header in filterHeaders"
              :key="header"
              :class="{ 'text-capitalize': true }"
            >
              {{ header }}
            </th>
            <td
              v-for="(analyzer, $analyzerIndex) in analyzers"
              :key="$analyzerIndex"
              class="no-newline"
              spellcheck="false"
              contenteditable="true"
              @keydown.enter.prevent="$event.target.blur()"
              @blur="setAnalyzerValue($event, analyzer)"
            >
              <!-- <v-text-field
                dense
                v-model="analyzerValues[$analyzerIndex]"
              ></v-text-field> -->
              {{ analyzerValues[$analyzerIndex] }}
              <span>
                <v-btn
                  icon
                  color="indigo"
                  @click="deleteAnalyzer"
                  contenteditable="false"
                  class="ml-3"
                >
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </span>
            </td>
          </thead>
          <tbody>
            <tr
              v-for="(field, $rowIndex) in register.fields"
              :key="$rowIndex"
              :class="{
                'grey--text text--darken-3': isRESERVED(field),
                'grey--text text--lighten-1': !isRESERVED(field),
                'text-left': true
              }"
            >
              <td
                v-for="header in filterHeaders"
                :key="header"
                contenteditable="false"
                v-html="$options.filters.highlight(field[header], highlight)"
                :style="{ 'font-size': fieldSize(field) }"
              >
                <!-- {{ field[header] }} -->
              </td>
              <td
                v-for="(analyzer, $analyzerIndex) in analyzers"
                :key="$analyzerIndex"
                contenteditable
                spellcheck="false"
                @keydown.enter="$event.target.blur()"
                @blur="setFieldValue($event, analyzer, $rowIndex)"
                class="no-newline"
                :style="{ 'font-size': fieldSize(field) }"
              >
                {{ `0x${analyzerFeildValues[$analyzerIndex][$rowIndex]}` }}
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card-text>

    <!-- </v-expand-transition> -->
  </v-card>
</template>

<script>
import utilities from '@/utilities.js'

export default {
  props: {
    register: {
      type: Object,
      default: null
    },
    headers: {
      type: Array,
      default: null
    },
    title: {
      type: String,
      default: ''
    },
    highlight: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      hasHover: false,
      is_open: false,
      analyzers: [],
      initialValue: '',
      bitWidth: null,
      initValues: [],
      showDetail: true,
      headerToKeep: ['msb', 'lsb', 'default', 'field', 'access']
    }
  },
  mounted() {
    for (const field of this.register.fields) {
      // console.log(field)
      const msb = parseInt(field.MSB)
      const lsb = parseInt(field.LSB)
      const initValue = field.default
      if (!this.bitWidth) {
        this.bitWidth = msb + 1
      }
      this.initValues.push(utilities.hexToBinary(initValue, msb - lsb + 1))
    }
  },
  methods: {
    addAnalyzer() {
      this.analyzers.push([...this.initValues])
    },
    setFieldValue(e, analyzer, index) {
      const newValue = e.target.innerText.trim()
      const bitwidth = analyzer[index].length
      const binary = utilities.hexToBinary(newValue, bitwidth)
      analyzer.splice(index, 1, binary)
    },
    setAnalyzerValue(e, analyzer) {
      const value = e.target.innerText.trim()
      const binary = utilities.hexToBinary(value, this.bitWidth)
      let start = 0
      for (let index = 0; index < analyzer.length; index++) {
        const len = analyzer[index].length
        analyzer.splice(index, 1, binary.slice(start, len + start))
        start += len
      }
    },
    binaryToHex(value, width = undefined) {
      return utilities.binaryToHex(value, width)
    },
    isRESERVED(field) {
      return field.field.toUpperCase() === 'RESERVED'
    },
    closeAction() {
      this.$emit('close', this.register.id)
    },
    // setWidth(str) {
    //   return this.headerToKeep.includes(str.toLowerCase())
    // },
    deleteAnalyzer(index) {
      this.analyzers.splice(index, 1)
    },
    highlightText(text, query) {
      return text
        .toString()
        .replace(query, '<span class="highlight">' + query + '</span>')
    },
    fieldSize(field) {
      return this.isRESERVED(field) ? '0.5em' : '1.1em'
    },
    toggleCardContent() {
      this.is_open = !this.is_open
      // window.getSelection().empty()
    }
  },
  computed: {
    analyzerValues() {
      return this.analyzers.map(
        item =>
          `0x${utilities
            .binaryToHex(item.join(''), this.bitWidth / 4)
            .toUpperCase()}`
      )
    },
    analyzerFeildValues() {
      return this.analyzers.map(items =>
        items.map(item =>
          utilities.binaryToHex(item, item.length / 4).toUpperCase()
        )
      )
    },
    filterHeaders() {
      if (this.showDetail) {
        return this.headers
      } else {
        return this.headers.filter(header =>
          this.headerToKeep.includes(header.toLowerCase())
        )
      }
    }
  }
}
</script>

<style>
.highlight {
  background-color: #455a64;
  /* text-decoration-color: yellow; */
}
</style>

<style scoped>
td {
  /* let table can recognize new line */
  white-space: pre-line;
  /* white-space: pre-wrap; */
}
.no-newline {
  white-space: normal;
  white-space: nowrap;
}

.mini {
  width: 1%;
}

.analyzer-card-title-hover {
  background-color: #292929;
}

.analyzer-card-title-open {
  white-space: pre-line;
}

.v-data-table
  /deep/
  tbody
  /deep/
  tr:hover:not(.v-data-table__expanded__content) {
  background: #292929 !important;
}
</style>
